var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"privacy-policy-area ptb-100"},[_vm._ssrNode("<div class=\"container\"><div class=\"privacy-policy-content\"><h6><a href=\"https://www.dealdox.io/privacy-policy\">Privacy Policy</a> for IT Consulting and Solutions Organization </h6> <p class=\"text-justify\">At our DealDox Pvt Ltd organization, we are committed to protecting the privacy and <a href=\"https://www.dealdox.io/security\">security</a> of our\n                clients' and website visitors' personal information. This privacy policy outlines how we collect, use,\n                and protect your personal information.</p> <p class=\"text-justify\">Unless a separate policy is stated, this Privacy Policy applies to the information we gather about you\n                when you use our products or services or otherwise engage with us (such as by visiting our location,\n                participating in an event, or interacting with us). DealDox Pvt Ltd affiliates are collectively referred\n                to as we and us. Our server, cloud, and data center offerings are just a few of the many things we have\n                to offer. All of these items are collectively referred to in this policy as &quot;Services,&quot; along with all\n                of our other offerings and websites.</p> <p class=\"text-justify\">This policy also outlines your options for how we use information about you, including how to object to\n                specific uses of that information and how to access and amend that information. </p> <h6>What Personal Information We Collect </h6> <p class=\"text-justify\">We gather information about you when you offer it to us, when you use our services, and when other\n                sources send it to us as below.</p> <h6> Profile Information</h6> <p class=\"text-justify\"> We collect information about you when you sign up for the services, create or modify your profile, set\n                preferences, subscribe to the Services, or make a purchase. When you register for the Services, for\n                instance, you provide your contact information and, in some cases, billing information. For your profile\n                information to be displayed in our Services, you can also choose to add a display name, a profile\n                picture, a job title, and other details. When you select settings in the Services, we keep track of your\n                preferences.</p> <h6>Information you provide through our services </h6> <p class=\"text-justify\">The DealDox services you use, where we collect and store the content you post, send, receive, and share.\n            </p> <h6>Information you provide via our websites </h6> <p class=\"text-justify\">We also own and operate our websites, which are included in the Services. We also collect any additional\n                content that you post to these websites, which include our social media and social networking websites.\n                When you give us feedback or participate in any interactive features, surveys, contests, promotions,\n                sweepstakes, activities, or events, for instance, you give us content. </p> <h6>Content you offer through our support platforms </h6> <p class=\"text-justify\">The Services also include our customer success, where you can submit information about a problem, you're\n                having with any of our Services. Whether you assign yourself as a specialized contact, open a help\n                ticket, address one of our delegates straightforwardly or engage with our support unit, you will be\n                requested to give contact data, a synopsis of the issue you are facing, and some other documentation,\n                screen captures or data that would be useful in fixing the issue. </p> <h6> Billing Information</h6> <p class=\"text-justify\">When you sign up for some paid services, we get data about your payment and billing. For instance, when\n                you register, we ask you to name and provide contact information for a billing representative. You can\n                also give us payment information, like your credit card information, which we get from you through\n                secure payment processing services. </p> <h6>When you use the Services, we automatically collect information from you </h6> <p class=\"text-justify\"> When you use our services, such as browsing our websites and performing particular actions within the\n                services, we collect information about you. </p> <h6>Cookies and Tracking Technologies </h6> <p class=\"text-justifyt\"> DealDox and our third-party allies, like our advertising and analytics partners, use cookies and other\n                tracking technologies (e.g., web beacons, device identifiers and pixels) to deliver functionality and to\n                identify you across different Services and devices. </p> <h6>Third Party Providers</h6> <p class=\"text-justify\"> We may obtain information about you from third-party suppliers of business information and publicly\n                accessible sources (such as social media platforms), such as physical postal addresses, job titles,\n                email addresses, phone numbers, intent data (or user behavior data), IP addresses, and social media\n                profiles, for the purposes of targeted advertising of items that may interest you, sending tailored\n                messages, event promotion, and profiling. </p> <h6>How We Use Your Information </h6> <p class=\"text-justifyt\"> The Services you use, how you use them, and any preferences you have communicated to us all impact how\n                we use the information we collect. The specific purposes for which we utilize the data we gather about\n                you are recorded underneath. </p> <h6>To customize your experience and provide the Services </h6> <p class=\"text-justify\"> We use information about you to operate, improve, and maintain the Services, provide customer support,\n                process transactions with you, verify your identity when you log in, and provide the Services to you.\n                For instance, we use your account name and picture to identify you to other Service users. By\n                automatically analyzing the activities of your team to provide search results, activity feeds,\n                notifications, connections, and recommendations that are most relevant to you and your team, tailored\n                features in our Services also personalize your experience, increase productivity, and improve your\n                ability to collaborate effectively with others. For instance, we may return search results that we\n                believe are relevant to your job function based on your stated job title and activity. We additionally\n                use data about you to associate you with other colleagues looking for your topic skill. To tailor the\n                content and experience you get on our websites, we may use your email domain to determine your\n                affiliation with a specific organization or industry. When you use multiple Services, we combine\n                information about you and your activities to provide an integrated experience. For example, we can show\n                you relevant product information as you move through our websites or let you find information from one\n                Service while searching for it in another. </p> <h6> For R&amp;D (Research and Development)</h6> <p class=\"text-justify\">We always look for ways to improve the usefulness, speed, security, integration, and intelligence of our\n                Services. We troubleshoot and identify trends, usage patterns, activity patterns, and areas for\n                integration using the information and collective learnings (including feedback) about how organizations\n                use our Services to enhance their businesses and digital transformation. </p> <h6>To advertise, promote, and encourage the use of the Services </h6> <p class=\"text-justify\"> We send you promotional communications that may be of particular interest to you using your contact\n                information and information about how you use the Services, such as via email and by displaying DealDox\n                advertisements on the websites and applications of other businesses. The purpose of these\n                communications, which may be informed by audits of interactions such as counting ad impressions, is to\n                encourage you to participate in the Services and make the most of them. For example, we might send you\n                information about new features, requests for surveys, newsletters, and events we think you might be\n                interested in. We will additionally speak with you about new solutions, service offers, upgrades, and\n                challenges. Under &quot;Opt-out of communications,&quot; you can choose whether or not to receive these\n                communications. </p> <h6>Customer Support </h6> <p class=\"text-justify\">We use your information to fix and improve the Services, to respond to your requests for assistance, to\n                analyze crash data, and to resolve technical issues you encounter. We share information with a\n                third-party expert to respond to support-related requests when you explicitly permit us to do so. </p> <h6>With your authorization </h6> <p class=\"text-justify\">When you have permitted us, we use information about you for a specific purpose that isn't listed above.\n                With your consent, we may, for instance, promote the Services by publishing customer stories or\n                testimonials. </p> <h6>Security and Safety </h6> <p class=\"text-justify\">We verify your accounts and activity, detect, prevent, and respond to potential or actual security\n                incidents, and monitor and protect against other malicious, deceptive, fraudulent, or illegal activity,\n                including violations of Service policies, using information about you and your use of the Service. </p> <h6>How We Protect Your Information </h6> <p class=\"text-justify\">To prevent unauthorized access, disclosure, modification, or destruction of your personal information, we\n                employ industry-standard security measures. We regularly review and update our security practices to\n                ensure that your personal information is safe. </p> <h6> Data Storage and Security</h6> <p class=\"text-justify\"> The data we store is protected by industry-standard organizational and technical safeguards. Even though\n                we take precautions to safeguard your information, we cannot guarantee that information transmitted over\n                the Internet, stored on our systems, or otherwise in our care will be completely secure from outside\n                intrusion. This is because no security system is completely secure.\n                You, not DealDox, are responsible for securing the storage and access to the information you enter into\n                our server or data center services. Users of servers or data centers should configure SSL to restrict\n                access to the databases and other storage locations used and to prevent the interception of information\n                sent over networks. </p> <h4>Access and Control Your Data </h4> <h6>Your Rights </h6> <p class=\"text-justify\">\n                You have the right to request a copy of your information, to object to our use of it (including for\n                marketing purposes), to have it deleted or restricted, or to receive it in an organized electronic\n                format. You may first need to get in touch with that service to get help with your requests. For all\n                other requests, please contact us using the information in the Contact Us section below.\n                In some cases, your request and options may be limited: for instance, if granting your request would\n                reveal information about another individual or if we or your administrator are required by law or have\n                compelling legitimate reasons to keep the information. If you have asked us to share your data with\n                third parties, such as by installing third-party apps, you will need to get in touch with those\n                third-party service providers directly to have your data deleted or restricted in some other way. You\n                may be able to file a complaint with a data protection authority in the nation in which you live, and\n                work, or believe your rights have been violated if you have unresolved concerns. </p> <h6>Request to stop using your information </h6> <p class=\"text-justify\">Sometimes, you might request that we quit getting to, putting away, utilizing and in any case handling\n                your data where you accept, we don't have the proper freedoms to do as such. You can, for instance,\n                request that we delete your Services account by this policy if you believe it was created for you\n                without your permission or if you are no longer an active user. You could contact us to withdraw your\n                consent to use your information for a limited purpose if you gave it to us, but this will not change any\n                processing that was already taking place at the time. By contacting us as outlined below, you can also\n                opt out of our use of your information for marketing purposes. When you make such requests, we might\n                need some time to investigate them and help you. </p> <h6>Opt-Out </h6> <p class=\"text-justify\"> You can unsubscribe from receiving promotional communications from us by updating your email preferences\n                in your Service account settings menu, using the unsubscribe link in each email, or by contacting us as\n                outlined below to have your contact information removed from our registration database or promotional\n                email list. You will continue to receive transactional messages from us regarding our Services even\n                after you opt out of receiving promotional messages. In your account settings, you can choose not to\n                receive certain notification messages. Please be aware that you will still receive generic\n                advertisements. </p> <h6> Notice to End Users</h6> <p class=\"text-justify\"> DealDox intends to provide services to businesses. Your use of the DealDox website may be subject to the\n                policies of your organization, if any. If your organization manages your access to the DealDox website,\n                please report any privacy concerns to your administrator. DealDox is not responsible for our clients'\n                privacy or security policies, which may differ from those outlined in this privacy statement.\n                If you use an email address supplied by an entity with which you are affiliated, such as your workplace\n                or school, the owner of the domain associated with your email address (e.g., your employer) may access\n                and handle your data, including the contents of your messages and files. </p> <h6>Updates to this Policy </h6> <p class=\"text-justify\">\n                This privacy statement is subject to modification at any moment. Any changes to our privacy policies\n                will be posted on this page, and if the changes are substantial, we will offer a more prominent notice\n                by including a notice on the Services homepages and login screens, or by sending you an email\n                notification. Prior versions of this Privacy Policy will also be archived for your perusal. We urge you\n                to examine our privacy policies anytime you use the Services so that you are aware of our information\n                practices and how you may help safeguard your privacy.</p> <p class=\"text-justify\">If you do not agree with any modifications to this privacy policy, you must discontinue use of the\n                Services and cancel your account(s), as described above. </p> <h5> Contact Us </h5> <p class=\"text-justify\"> If you have any questions or concerns about our privacy policy, please contact us at <strong>\n                    info@dealdox.com</strong> We shall get in touch with you within 30 days. </p> <p class=\"text-justify\"><strong>DealDox Pvt Ltd #63/3, Arham Towers, Krishna Rajendra Road, 7th Block, Jayanagar, Bengaluru -\n                    560070, India. </strong></p></div></div>")])
